.makingof-container {
  display: grid;
  grid-template-rows: 1fr 1fr;
  .makingof-item {
    min-height: 70vh;
    display: grid;
  }
  .makingof1 {
    grid-template-columns: 2fr 1fr;
  }
  .makingof2 {
    grid-template-columns: 1fr 2fr;
  }
}

.gif1 {
  background: url(../..//img/1.gif) bottom right / cover;
}
.gif2 {
  background: url(../..//img/2.gif) bottom right / cover;
}

.text-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  p {
    line-height: 2rem;
    max-width: 80%;
    padding-bottom: 20px;
  }
}
