footer {
  background: white;
  margin: 30px 10vh 0 10vh;
  min-height: 80px;
  font-family: SourceSans;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: space-around;
  div {
    text-align: center;
    a {
      color: grey;
      &:hover {
        text-decoration: underline;
        color: black;
      }
    }
    ul {
      padding: 10px;
      li {
        font-size: 0.9rem;
        #comingSoon {
          color: rgb(138, 5, 5);
          font-size: 0.7rem;
        }
      }
    }
  }
}
