#popup {
  transition: 0.1s ease;
  z-index: 1000;
  position: absolute;
  left: 10px;
  width: 20%;
  border: 1px solid ghostwhite;
  border-radius: 10px;
  background: linear-gradient(
    90deg,
    rgba(48, 24, 15, 0.85),
    rgba(90, 37, 17, 0.85)
  );
  color: white;
  &:hover {
    transform: scale(1.02);
  }
  .popup-container {
    position: relative;
    h4 {
      padding-top: 10px;
      text-align: center;
      font-size: 1.2rem;
      font-family: Inter;
    }
    p {
      padding: 10px;
      font-size: 1.2rem;
      font-family: SourceSans;
      font-style: normal;
    }
    img {
      transition: 0.2s;
      position: absolute;
      top: 10px;
      right: 15px;
      width: 20px;
      filter: invert(90%) sepia(88%) saturate(5358%) hue-rotate(180deg)
        brightness(138%) contrast(116%);
      border: 1px solid blue;
      border-radius: 20px;
      cursor: pointer;
      &:hover {
        transform: rotate(360deg);
      }
    }
    a {
      color: white;
      text-decoration: underline;
      &:hover {
        font-weight: bold;
      }
    }
  }
}

@keyframes closeAnim {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    visibility: hidden;
  }
}
