@media screen and (max-width: 890px) {
  body {
    background: url(../img/bg-mobile.jpg) center/cover fixed;
  }
  header {
    height: 94vh;
    h1 {
      height: 100px;
      font-size: 1.9rem;
      margin: 0 18%;
    }
    p {
      font-size: 1rem;
      padding: 20px 10px 30px 10px;
      line-height: 20px;
    }
  }
  nav {
    left: 0;
    right: 0;
    .nav-pages a {
      font-size: 0.6rem;
      padding: 0 12px;
    }
    .nav-social {
      min-width: unset;
      max-width: 120px;
      li {
        padding: 0 8px;
        a {
          i {
            font-size: 0.8rem;
          }
        }
      }
    }
  }
  main {
    margin: 0;
    padding-bottom: 40px;
    h2 {
      font-size: 1.6rem;
      letter-spacing: 1px;
    }
    .intro .img-description {
      font-size: 0.7rem;
      right: 5%;
    }
    img {
      width: 90%;
    }
    p {
      font-size: 0.9rem;
      padding: 10px;
      line-height: 20px;
    }
    .intro {
      padding-bottom: 20px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.205);
    }
    .section-1 {
      padding: 0;
    }
    .section-2 {
      box-shadow: none;
      .container {
        margin-top: 20px;
      }
    }
    .section-1,
    .section-2 {
      .section-box {
        &:nth-child(1) {
          padding-left: 0;
        }
        &:nth-child(2) {
          padding-right: 0;
        }
      }
      display: unset;
      grid-template-columns: none;
      .container {
        padding: 10px 0;
        display: block;
      }
      .img-description {
        right: 5%;
      }
      img {
        width: 90%;
        &:nth-child(1) {
          padding-top: 70px;
        }
      }
    }
  }
  footer {
    background: white;
    @media screen and (max-width: 700px) {
      padding-top: 10px;
      flex-direction: column;
    }
    background: rgb(218, 218, 218);
    margin: 0;
  }
}

@media screen and (max-width: 690px) {
  header .website-intro-holder {
    margin: 30px 0;
    .website-intro {
      padding: 10px;
      max-width: none;
      border-radius: 0;
    }
  }
  .makingof-container {
    .makingof1 {
      grid-template-columns: none;
      grid-template-rows: 1fr 1fr;
    }
    .makingof2 {
      grid-template-columns: none;
      grid-template-rows: 1fr 1fr;
    }
  }
  #popup {
    width: 80%;
    left: 10%;
    .popup-container {
      h4 {
        font-size: 1.1rem;
      }
    }
  }
}

@media screen and (max-width: 405px) {
  .ask-container #questionsBox {
    padding-top: 10px;
  }
}

@media screen and (max-width: 320px) {
  .ask-container #questionsBox {
    padding-top: 30px;
  }
}

@media screen and (max-width: 270px) {
  .ask-container #questionsBox {
    padding-top: 50px;
  }
}

@media screen and (max-width: 255px) {
  .ask-container #questionsBox {
    padding: 80px 0;
  }
}

@media screen and (max-width: 770px) {
  #popup {
    .popup-container {
      p {
        font-size: 1rem;
      }
    }
  }
}
