.chat-app {
  border: 1px solid rgb(157, 157, 157);
  margin: 0 10%;
}

.input-container {
  border: 1px solid gray;
  input {
    padding: 10px;
    font-size: 1rem;
    border: 0;
    outline: 0;
    &[type="text"] {
      @media screen and (max-width: 790px) {
        width: 70%;
      }
      width: 90%;
    }
    &[type="submit"] {
      @media screen and (max-width: 790px) {
        width: 30%;
      }
      width: 10%;
      &:hover {
        background: rgb(202, 202, 202);
      }
    }
  }
}
.ask-container {
  overflow-y: scroll;
  height: 400px;
  position: relative;
  #answersBox,
  #questionsBox {
    display: block;
    width: 100%;
    height: 60px;
    @media screen and (max-width: 890px) {
      height: 15vh;
    }
    position: relative;
  }
  #answer,
  #question {
    position: absolute;
    font-family: SourceSans;
    font-weight: bold;
    display: inline-block;
    margin: 10px;
    padding: 10px;
  }
  #answer {
    background: linear-gradient(90deg, rgb(254, 143, 254), rgb(255, 189, 255));
    border-radius: 20px 20px 20px 0;
  }
  #question {
    background: linear-gradient(90deg, rgb(136, 170, 255), rgb(83, 149, 255));
    border-radius: 20px 20px 0 20px;
    right: 0;
  }
}

.version-name {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 0.9rem;
  font-family: SourceSans;
  font-weight: bold;
  padding: 5px 12px 5px 10px;
  border-radius: 0 0 8px 0;
  color: ghostwhite;
  background: linear-gradient(140deg, rgb(82, 82, 82), rgba(0, 0, 0, 0.438));
}
